import React , {  useEffect }  from "react";
import SEO from "../../components/seo"
import Layout from "../../components/layout";
import { CircleArrow as ScrollUpButton } from "react-scroll-up-button";
import Training from '../../templates-info/training_program';
const uuidv4 = require('uuid/v4');


const scroll_button = {
    width:'55px',
    height:'55px',
    padding:"4px",
    focus:'none',
    outline:'none',
    border:'0px',
    boxShadow:'0px 0px 2px 2px black',
    zIndex:100
  }



const Cart = () => {


const [ cart , setCart ] = React.useState(null)

  useEffect(() => {
    const cartItems = JSON.parse(localStorage.getItem("cart"));
    const cartItem = [];
      if(cartItems !== null){
        cartItems.map( x => {
           const newItem = Training.find(y => y._id === x.id)
           newItem.quantity = x.quantity;
          return cartItem.push(newItem)
      })
    }
     return setCart(cartItem)
  },[]);


 
const plusCount = (e , q ) => {
    let currentStore = JSON.parse(localStorage.getItem("cart")) || []
    currentStore.forEach(x => x.id === e?x.quantity += 1:false)
    localStorage.setItem("cart", JSON.stringify(currentStore))

    const itemToChange = cart.find(x => x._id === e)
    itemToChange.quantity = q + 1
    const newCart = cart.map(x => x._id === e?x = itemToChange:x);

   setCart(newCart)

}


const minusCount = (e , q) => {
    const itemToChange = cart.find(x => x._id === e)
    if(itemToChange.quantity !== 1){
       itemToChange.quantity = q - 1
       let currentStore = JSON.parse(localStorage.getItem("cart")) || []
       currentStore.forEach(x => x.id === e?x.quantity -= 1:false)
       localStorage.setItem("cart", JSON.stringify(currentStore))
    }
    const newCart = cart.map(x => x._id === e?x = itemToChange:x);
    setCart(newCart)
 }

const removeItem = id => {
    let currentStore = JSON.parse(localStorage.getItem("cart")) || []
    const newCart = cart.filter(x => x._id !== id);
    setCart(newCart)
    const newStorage = currentStore.filter(x => x.id !== id);
    localStorage.setItem("cart", JSON.stringify(newStorage))
}



  if(cart === null || !cart.length){
    return  <Layout>
            <SEO title="training_program" />
            <section className ="container container_cart">
            <h1>SHOPPING CART</h1>
            <h3>Your Cart is Empty</h3>
            </section>
           </Layout>
  }

    return (
        <Layout>
        <SEO title="training_program" />
        <ScrollUpButton style={scroll_button} />
        <section className ="container container_cart">
        <h1>SHOPPING CART</h1>
            <ul>
        {cart &&  cart.map(x => <li key = {uuidv4()}>
            <div className="cart_item-left">
            <p>{x.heading}</p>
            <img alt={x.alt} src = {x.main_img} />
            <button onClick ={() => removeItem(x._id)}>Remove</button>
            </div>
            <div>
            <div className="number_cart">
	             <span className="minus_cart" onClick={() => minusCount(x._id , x.quantity)} >-</span>
	                <input type="text"  value={x.quantity} disabled />
	             <span className="plus_cart" onClick={() => plusCount(x._id, x.quantity)} >+</span>
            </div>
            <p>${x.price * x.quantity}</p>
            </div>
          </li>
        )}
        </ul>

        <p><strong style={{fontSize:"1.2rem"}}>Total: ${cart.reduce((x,y) => x + (y.quantity * y.price ),0)}</strong></p>
       </section>
        </Layout>
    )
};

export default Cart;